.visually-hidden {
    position: absolute;
    height: 1px;
    width: 1px;
    overflow: hidden;
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px);
    white-space: nowrap;
}

.my-range {
    @media (min-width: 500px) {
        max-width: 300px;
        width: 100%;
    }
    @media (min-width: 768px) {
        max-width: 450px;
    }
    @media (min-width: 1170px) {
        max-width: 512px;
    }
}

@mixin rangeThumb {
    width: 23px;
    height: 23px;
    margin: -10px 0 0;
    border-radius: 50%;
    background: #f6ae2d;
    cursor: pointer;
    border: 0 !important;
}

@mixin rangeTrack {
    width: 100%;
    height: 4px;
    cursor: pointer;
    background-image: linear-gradient(to right, #f6ae2d 0%, #f6ae2d 80%, #e8e8e8 80%, #e8e8e8 100%);
}

.range {
    position: relative;
    bottom: -2px;
    width: 100%;
    height: 5px;
    background: linear-gradient(to right, #f6ae2d 0%, #f6ae2d 80%, #fff 80%, #fff 100%);
    @media (min-width: 1170px) {
        position: relative;
        &::before {
            content: '';
            position: absolute;
            top: 0px;
            right: -125px;
            background-color: #e8e8e8;
            display: block;
            width: 125px;
            height: 4px;
        }
    }
}

.range input {
    width: 100%;
    position: absolute;
    top: 0px;
    height: 0;
    -webkit-appearance: none;
    // Thumb
    &::-webkit-slider-thumb {
        -webkit-appearance: none; // needed again for Chrome & Safari
        @include rangeThumb;
    }
    &::-moz-range-thumb {
        @include rangeThumb;
    }
    &::-ms-thumb {
        @include rangeThumb;
    }
    // Track
    &::-webkit-slider-runnable-track {
        @include rangeTrack;
    }
    &::-moz-range-track {
        @include rangeTrack;
    }
    &::-ms-track {
        @include rangeTrack;
    }
    &:focus {
        // override outline/background on focus
        background: none;
        outline: none;
    }
    &::-ms-track {
        // A little somethin' somethin' for IE
        width: 100%;
        cursor: pointer;
        background: transparent;
        border-color: transparent;
        color: transparent;
    }
}

// Labels below slider
.range-labels {
    padding: 0;
    list-style: none;
    margin-top: 18px;
    display: flex;
    justify-content: space-between;
    li {
        position: relative;
        width: 10px;
        text-align: center;
        color: #000000;
        font-weight: bold;
        cursor: pointer;
        font-size: 14px;
        &:first-child {
            padding-left: 9px;
            @media (min-width: 768px) {
                padding-left: 4px;
            }
        }
        @media (min-width: 768px) {
            font-size: 18px;
            width: 14px;
        }
        &::before {
            position: absolute;
            top: -28px;
            right: 0;
            left: 0;
            content: "";
            margin: 0 auto;
            width: 18px;
            height: 18px;
            background: #ffffff;
            box-shadow: 0 2px 10px 6px rgba(0, 0, 0, 0.07);
            border-radius: 50%;
        }
    }
    .active {
        color: #000000;
    }
    .selected::before {
        background: #f6ae2d;
    }
    .active.selected::before {
        display: none;
    }
}


